import { useEffect, useRef, useState } from 'react'
import { useSelector } from 'react-redux'
import { Divider } from 'antd'
import axios from 'axios'
import React from 'react'
import { Button, Modal, Card, Container, Row } from 'react-bootstrap'

const LabResults = () => {
  const patient = useSelector((state) => state.loggedInRoleUserData.loggedInRoleUserData)
  const regex = /AMN-DOC-[A-Za-z0-9]+/
  const [reports, setReports] = useState([])
  const [show, setShow] = useState(false)
  const printRef = useRef(null)

  const handleClose = () => setShow(false)
  const handleShow = () => setShow(true)

  //eslint-disable-next-line
  const getBills = async () => {
    try {
      const patientId = await patient.uniqueId
      const res = await axios.post('/api/v1/patient/patientLabReportsHistory', { patientId })
      if (res.data.success) {
        console.log(res.data.data)
        setReports(res.data.data)
      }
    } catch (error) {
      console.log(error)
    }
  }
  useEffect(() => {
    getBills()
  }, [])
  return (
    <>
      <h4>Lab Results</h4>

      {/* Sample Starts from here */}

      {reports && reports.length > 0 ? (
        reports.map((report, idx) => (
          <Container ref={printRef} id={idx} fluid as={Card} className="my-3" key={idx}>
            <Row>
              <Card>
                {/* Date Containe */}
                <Container fluid className="d-flex justify-content-between mx-2 my-2">
                  <div
                    style={{
                      fontSize: '1.1rem',
                    }}
                  >
                    <b>Date</b> :{new Date(report.updatedAt).toLocaleDateString()}
                    <Divider type="vertical" />
                    <b>Report :</b> {report.reportName}
                  </div>
                </Container>
                <Divider className="my-1" />

                {/* Prescription Containe */}
                <Container fluid>
                  <Row className="d-flex justify-content-between">
                    <div className="col-12 col-lg-4 col-sm-5">
                      <div
                        style={{
                          fontSize: '1.1rem',
                          whiteSpace: 'nowrap',
                        }}
                        className=""
                      >
                        <b>Report ID :</b>
                        {report._id}
                      </div>
                      <div
                        style={{
                          fontSize: '1.1rem',
                        }}
                        className=""
                      >
                        <b>Requested By :</b>Dr. {report.requestedBy.split(regex)[0].trim()}
                      </div>
                      <div
                        style={{
                          fontSize: '1.1rem',
                        }}
                        className=""
                      >
                        <b>Payment Mode :</b>
                        {report.paymentMode}
                      </div>
                    </div>
                  </Row>
                  <Container className="d-flex justify-content-end align-items-center flex-column flex-sm-column flex-md-row flex-lg-row">
                    <Button
                      className="m-2 btn-dark col-lg-2 col-md-2 col-12"
                      style={{
                        whiteSpace: 'nowrap',
                      }}
                      onClick={() => {
                        const printWindow = window.open('', '_blank')
                        printWindow.document.write('<html><head><title>Aman Hospital</title>')
                        printWindow.document.write('<style>@media print { @page { size: 148mm 70mm; margin: 0; } }</style>');
                        printWindow.document.write('</head><body>')

                        // Update the content to include hospital name and contact number
                        printWindow.document.write('<h1>Aman Hospital</h1>')
                        printWindow.document.write('<p>Contact: 9876543210</p>')

                        printWindow.document.write(printRef.current.innerHTML)
                        printWindow.document.write('</body></html>')
                        printWindow.document.close()
                        printWindow.print()
                      }}
                    >
                      Download
                    </Button>
                    <Button
                      className="m-2 btn-dark col-lg-2 col-md-2 col-12"
                      style={{
                        whiteSpace: 'nowrap',
                      }}
                      onClick={handleShow}
                    >
                      View
                    </Button>
                  </Container>
            
                  <Modal show={show} onHide={handleClose} animation={false}>
                    <Modal.Header closeButton>
                      <Modal.Title>Lab Report</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                      <Container>
                        <Row>
                          <div className="col-12 col-lg-4 col-sm-5">
                            <div
                              style={{
                                fontSize: '1.1rem',
                                whiteSpace: 'nowrap',
                              }}
                            >
                              <b>Report ID :</b>
                              {report._id}
                            </div>
                            <div
                              style={{
                                whiteSpace: 'nowrap',
                                fontSize: '1.1rem',
                              }}
                            >
                              <b>Requested By :</b>Dr. {report.requestedBy.split(regex)[0].trim()}
                            </div>
                            <div
                              style={{
                                fontSize: '1.1rem',
                                whiteSpace: 'nowrap',
                              }}
                            >
                              <b>Payment Mode :</b>
                              {report.paymentMode}
                            </div>
                            <div
                              style={{
                                fontSize: '1.1rem',
                                whiteSpace: 'nowrap',
                              }}
                            >
                              <b>Report Name :</b>
                              {report.reportName}
                            </div>
                            <div
                              style={{
                                fontSize: '1.1rem',
                                whiteSpace: 'nowrap',
                              }}
                            >
                              <b>Date :</b>
                              {new Date(report.updatedAt).toLocaleDateString()}
                            </div>
                            <div
                              style={{
                                fontSize: '1.1rem',
                                whiteSpace: 'nowrap',
                                height: '10rem',
                                width: '10rem',
                              }}
                            >
                              <b>Report File :</b>
                              <img
                                style={{
                                  height: '10rem',
                                  width: '10rem',
                                }}
                                src={report.reportFile}
                                alt="Report File"
                              />
                            </div>
                          </div>
                        </Row>
                      </Container>
                    </Modal.Body>
                    <Modal.Footer>
                      <Button variant="secondary" onClick={handleClose}>
                        Close
                      </Button>
                      <Button variant="primary" onClick={handleClose}>
                        Save Changes
                      </Button>
                    </Modal.Footer>
                  </Modal>
                </Container>
              </Card>
            </Row>
          </Container>
        ))
      ) : (
        <>
          <Container>No data Found for Bills</Container>
        </>
      )}
      {/* Sample Ends here */}
    </>
  )
}

export default LabResults
